<template>
<div class="authentication">
  <div class="record-bar">
    <van-nav-bar
      :title="$t('AQBTS')"
      left-arrow
      @click-left="linkHome"
    />
  </div>
  <div class="title">{{$t('inputFP')}}</div>
  <div class="input">
    <!-- 密码输入框 -->
    <van-password-input
      :value="Password"
      :focused="showKeyboard"
      @focus="showKeyboard = true"
    />
    <!-- 数字键盘 -->
    <van-number-keyboard
      :show="showKeyboard"
      @input="onInput"
      @delete="onDelete"
      @blur="showKeyboard = false"
    />
  </div>
</div>
</template>

<script>
  import { Toast } from 'vant';
  export default {
  name: 'authentication',
  data () {
    return {
      // 密码
      Password: '',
      // 键盘显示
      showKeyboard: true
    }
  },
  created(){
    console.log(this.$route.params.form)
  },
  methods: {
    // 键盘输入事件
    onInput (key) {
      this.Password = (this.Password + key).slice(0, 6)
      if (this.Password.length === 6) {
        let that = this;
        if (that.global.ws && that.global.ws.readyState == 1) {
          that.global.ws.onopen = that.setFundPassword()
        }
        that.global.ws.onmessage = function(res) {
          that.getFundPassword (res)
        };
        // this.$router.push({
        //   name: 'setFundPassword'
        // })
      }
    },
    setFundPassword(){
      let actions;
      let that = this;
      actions = {
        action: "verifyDrawPass",
        reqID: 63,
        data: {
          id: this.uid,
          oldPass:this.Password
        }
      } // 请根据实际项目需要进行修改
      that.global.ws.send(JSON.stringify(actions))
    },
    getFundPassword(e){
      let that = this
      this.redata = JSON.parse(e.data)
      console.log(this.redata)
      if(this.redata.data.code === 0 && this.redata.action === "verifyDrawPass"){
        if(this.$route.params.form === 'accountManage'){
          this.$router.push({
            name:'replaceBankCard',
            params:{
              oldPass:this.Password
            }
          })
        }
      }else if(this.redata.action === "verifyDrawPass"){
        Toast.fail({
          duration:1000,
          message:that.redata.data.msg,
          onClose() {
            that.Password = ''
          }
        })
      }
    },
    // 键盘删除事件
    onDelete () {
      this.Password = this.Password.slice(0, this.Password.length - 1)
    },
    // 返回上级
    linkHome () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">
  @import "../../../style/my";
</style>



